html {
  display: flex;

  flex: 1;
  height: 100%;
}

body {
  display: flex;
  flex: 1;

  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: flex;

  flex: 1;
  align-self: stretch;
}

@font-face {
  font-family: 'Marvel-Regular';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Marvel-Regular.ttf');
}

@font-face {
  font-family: 'Marvel-Bold';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Marvel-Bold.ttf');
}

@font-face {
  font-family: 'Marvel-Italic';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Marvel-Italic.ttf');
}

@font-face {
  font-family: 'Marvel-Bold-Italic';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Marvel-BoldItalic.ttf');
}